.QuestionItem {
  background-color: var(--main-color);
  padding: 0.5vw;
  border-radius: 5vw;
  font-size: 1.8vw;
  box-shadow: inset var(--box-shadow);
  align-items: center;
  transition: all .3s;
  cursor: pointer;
  position: relative;
  height: 4.5vw;
  transition: all .3s;
  display: inline-block;
  margin-bottom: 1vw;
  .QuestionItem-icon {
    position: absolute;
    width: 3.5vw;
    height: 3.5vw;
    background-color: white;
    border-radius: 50%;
    box-shadow: var(--box-shadow);
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all .3s;
    .iconfont {
      color: var(--main-color);
      font-size: 3vw;
      transition: all .3s;
      transition-delay: .3s;
    }
  }
  span {
    padding: 0 1vw; 
    transition: all .3s;
    line-height: 3.8vw;
    a {
      color: white;
    }
  }
}

.checked {
  background-color: var(--deep-blue);
  color: white;
  .QuestionItem-icon {
    background-color: var(--main-color);
    .iconfont {
      color: white;
      transform: rotate(90deg);
    }
  }
}
@primary-color: #FF2A42;