.Questions {
  // background-color: red;
  margin: 0 6%;
  margin-bottom: 12vw;
  z-index: 9;
  position: relative;
  // 头部
  .Questions-header {
    color: white;
    margin: 2.5vw 0;
    .Questions-header-title {
      font-size: 8vw;
      text-shadow: var(--box-shadow);
    }
    .Questions-header-subTitle {
      margin: 2vw 0 3vw 0;
      font-size: 2.8vw;
      padding-left: 0.8vw;
      .daily-color {
        color: #E32831;
      }
      .weekly-color {
        color: #686884;
      }
    }
    .Questions-header-line {
      width: 17vw;
      border-radius: 1vw;
      height: 1vw;
      margin-left: 1vw;
      background-color: white;
      box-shadow: var(--box-shadow);
    }
  }
  // 中间内容
  .Questions-content {
    height: 50vw;
    background-color: white;
    border-radius: 1vw;
    padding: 3%;
    display: flex;
    position: relative;
    .Questions-content-year {
      position: absolute;
      right: 7%;
      top: -3.5vw;
      font-size: 6vw;
      font-weight: bolder;
      color: var(--deep-blue);
      text-shadow: var(--box-shadow);
    }
    .Questions-content-left {
      padding: 5vw;
      font-size: 5vw;
      color: var(--deep-blue);
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    .Questions-content-center-line {
      width: 2vw;
      height: 100%;
      background-color: var(--main-color);
      border-radius: 1vw;
      box-shadow: inset var(--box-shadow);
    }
    .Questions-content-right {
      padding: 4vw 4vw;
      flex: 1;
      .Questions-content-right-day {
        .day {
          font-size: 4vw;
          color: var(--deep-blue);
          position: relative;
          &::after {
            position: absolute;
            content: '';
            width: 3.5vw;
            height: 3.5vw;
            border-radius: 50%;
            background-color: var(--main-color);
            left: -6.8vw;
            top: 0.5vw;
            box-shadow: inset var(--box-shadow);
          }
        }
        .day-container {
          padding: 2vw 0;
        }
      }
    }
  }
}
@primary-color: #FF2A42;